<template>
	<div class="page has-navbar" v-nav="{hideNavbar:true}" style="padding-top: 50px;">
		<von-header>
			<button class="button button-icon ion-ios-arrow-back" slot="left" @click="back"></button>
			<span slot="title" @click="back"></span>
		</von-header>
		<von-input type="text" v-model="from.pd_title" placeholder="" label=""></von-input>
		<datepicker v-model="from.pd_startdate" label="" date-format="yyyy-mm-dd"></datepicker>
		<datepicker v-model="from.pd_enddate" label="" date-format="yyyy-mm-dd"></datepicker>
		<md-button class="button button-positive" style="  width: 80%; margin-left: 10%;  margin-top: 30px;" @click.native="push">
			
		</md-button>
	</div>
</template>
<script>
	import util from '../../static/util';
	export default {
		data() {
			return {
				from: {
					pd_title: '',
					pd_startdate: util.getDate(),
					pd_enddate: util.getDate(),
				}
			}
		},
		created() {
			debugger;
		},
		mounted: function() {

		},
		methods: {
			push: function() {
				debugger;
				if(this.from.pd_title == '') {
					$toast.show('!', 1500);
					return;
				}
				var a = parseInt(this.from.pd_startdate.replace(/-/g, ""));
				var b = parseInt(this.from.pd_enddate.replace(/-/g, ""));
				if(a > b) {
					$toast.show('!', 1500);
					return;
				}

				var data = this.from;
				data.randomid = this.$route.query.randomid;
				data.uid = this.$route.query.uid;
				data.zhuti = this.$route.query.zhuti;
				data.gl_jigouhao =  this.$route.query.gl_jigouhao; 
				$loading.show('...');
				util.ajax.post(util.pushdata, data).then(res => {
					debugger;  
					if(res.status == 200) {
						$toast.show(res.message, 1500).then(() => {
							this.back('/');
						});
					}else{
						$toast.show(res.message, 1500);
					} 
					//$loading.hide();
				}).catch(err => {
					$toast.show('!', 1500);
					console.log(err); 
				});
			},
			back: function() {
				$router.back('/')
			}
		}
	}
</script>

<style>
	.input-label {
		font-size: 16px !important;
	}
</style>


