<template>
	<div class="page-content padding padding-top">
		<scroller ref="scroller2">
			<div class="list von-radio" style="padding-top: 44px;">
				<label class="item item-borderless item-icon-left" v-for="(item,idx) in orgOptions" @click="check(idx)">
				<div class="hairline-top"> </div> 
			<input type="radio" name="von-radio-ppn" :value="idx"> 
				<i class="icon ion-ios-checkmark" :class="org==idx?'assertive':'grey'"></i> <span>{{item}}</span> 
			</label>
			</div>
			 
		</scroller>
	</div>
</template>
<script>
	import util from '../../static/util';
	export default {
		data() {
			return {
				org: 0,
				orgOptions: [],
			}
		},
		created() {
			this.org = util.checkOrg;
			var data = [];
			for(var i = 0; i < util.Organization.length; i++) {
				data.push(util.Organization[i].value);
			}
			this.orgOptions = data;
			debugger;
		},
		mounted: function() {
			this.$nextTick(function() {
				setTimeout(() => {
					if(this.$refs.scroller2)
						this.$refs.scroller2.resize()
				});
			});
		},
		methods: {
			check: function(idx) {
				this.org = idx;
				util.checkOrg = idx;
				return false;
			}
		}
	}
</script>

<style>

</style>


